<template>
  <div class="shadow">
    <Line
      :chartData="config.data"
      height="0"
      width="0"
    />
  </div>
</template>

<script>
import {Line} from 'vue-chartjs'
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  PointElement,
  CategoryScale,
  Plugin
} from 'chart.js'

ChartJS.register(
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  PointElement,
  CategoryScale
)
export default {
  name: 'linechart',
  components: {Line},
  data() {
    return {
      config: {
        type: 'line',
        data: {
          labels: ['январь', 'февраль', 'март', 'апрель', 'май', 'июнь', 'июль', 'август', 'сентябрь', 'октябрь', 'ноябрь', 'декабрь'],
          datasets: [{
            label: 'график',
            data: [65, 59, 80, 81, 56, 55, 40, 12, 13, 14, 15, 16],
            fill: false,
            borderColor: 'rgb(75, 192, 192)',
            tension: 0.1
          }]
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
div {
  padding: 16px;
  border-radius: var(--border-radius);
  background: var(--bg-color);

  canvas {
    max-width: 100%;
    max-height: 500px;
  }
}
</style>
